const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			navList: [
				{ id: '3', img: 'iconbulletin', title: '微金公告',type: '23'},
				{ id: '6', img: 'iconqiapiansousuo', title: '论坛搜索',type: '' },
				{ id: '2', img: 'iconliebiao', title: '贷款产品交流',type: '1'},	
				{ id: '4', img: 'iconyonghu', title: '信用卡交流',type: '2' },
				{ id: '5', img: 'iconqiapiansousuo', title: '推荐热帖',type: '3' },
				{ id: '1', img: 'iconxiepinglun', title: '网友交流',type: ''},
			],
			imgUrl:'',
			isShow: false,
			categoryList:[],
			subCategoryList:[],
			categoryActive: 0,
			area_id: ''
		};
	},
	onLoad() {	
		this.imgUrl = helper.imgUrl
	},
	onShow(){
		this.getUserInfo()
		this.categoryActive = 0,
		// 加载微金交流首页数据
		this.getRegion();
	},
	methods: {
		// 左边导航点击事件
		categoryMainClick(e,index){
			console.log(e,index);
			this.categoryActive = index;
			this.area_id = e
			this.subCategoryList =[]
			this.getList()
		},
		// 详情
		categorySubClick(title,id,img,fatheId){
			uni.navigateTo({
				url:`/pages/exchangList?title=${title}&id=${id}&img=${img}&fatheId=${fatheId}`
			})
		},
		// 获取用户信息
		getUserInfo(){
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					let user = res.data.type
					if(user == 'normal'){
						this.isShow = true
					}else{
						this.isShow = false
					}
					// this.userInfo = res.data
				}
			})
		},
		// 数据
		getRegion() {
			uni.request({
				url: `${helper.requestUrl}/areas/area-list`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.categoryList = res.data.data;
						this.area_id = this.categoryList[0].id
						this.getList()
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 导航子页面跳转
		getNav(id,title,img) {
			if(id != ''){
				uni.navigateTo({
					url: `/pages/indexA?id=${id}&name=${title}`
				});
			}else{
				if(title == '网友交流'){
					uni.navigateTo({
						url:`/pages/boardData?title=${title}&id=${38}&img=${img}`
					})
				}
				
			}
			
		},
		// 加载下一页
		getList() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/areas/city-list`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					area_id: this.area_id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.subCategoryList = res.data.data;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
	}
};
